import * as Yup from "yup";

import { useTranslation } from "react-i18next";


export  default function Validation(){
const { t } = useTranslation();
  const LoginSchema = Yup.object().shape({
    email: Yup.string()
      .matches(
        /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
        `${t('login.emailMatches')}`
       
      )
      .required(`${t('login.emailRequired')}`),
    password: Yup.string()
  
      .min(8,  `${t('login.passwordLength')}`)
      .required( `${t('login.passwordRequired')}`)
      .matches(/(?=.*[0-9])/,  `${t('login.passwordMatches')}`),
  });
   const RegisterSchema = Yup.object().shape({
    username: Yup.string()
      .required(`${t('signup.required')}`)
      .matches(/^[ أ-ي ]{3,18}$/i, `${t('signup.matches')}`),
    usernameEn: Yup.string()
      .required(`${t('signup.required')}` )
      .matches(/^[a-zA-Z]{3,15}$/i, `${t('signup.matches')}`),
    nationality: Yup.string()
      .required(`${t('signup.required')}`)
      .matches(/[أ-ي]/, `${t('signup.matches')}`),
    id: Yup.string()
      .required(`${t('signup.required')}`)
      .matches(/[0-9]/, `${t('signup.matches')}`),
    phone: Yup.string()
      .required(`${t('signup.required')}`)
      .matches(/[0-9]/, `${t('signup.matches')}`)
      .min(11, `${t('signup.phoneLength')}`),
    email: Yup.string()
      .matches(
        /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
        `${t('signup.matches')}`
      )
      .required(`${t('signup.required')}`),
    job: Yup.string()
      .matches(/[أ-ي]/,  `${t('signup.matches')}`)
      .required(`${t('signup.required')}`),
  
    qualification: Yup.string()
    .required(`${t('signup.required')}`),
    sector: Yup.string()
    .required(`${t('signup.required')}`),
    admission: Yup.boolean().oneOf([true], `${t('signup.checkboxError')}`),
  });
  return{
    RegisterSchema,
    LoginSchema
  }

}

