import { Formik, Form, Field, ErrorMessage } from "formik";
import logo from "../../assets/images/safa-logo.svg";
import google from "../../assets/images/google.png";
import group7 from "../../assets/images/Group7.png";
import group342 from "../../assets/images/Group342.png";
import  styles from "./styles.module.scss";
import Validation from "../../core/Helpers/validation";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faFacebookSquare,
  faTwitter,
} from "@fortawesome/free-brands-svg-icons";
import { useTranslation } from "react-i18next";
export default function Login() {
  const { t } = useTranslation();
  return (
    <div className={`${styles.main} d-flex align-items-center justify-content-center flex-column py-5`}>
      <div className={styles.top}>
        <img src={group7} alt="circles" className="w-100" />
      </div>
      <div className="mb-5">
        <img src={logo} alt="logo" />
      </div>
      <div
        className={`${styles.bgLight} d-flex align-item-center flex-column  p-5 mx-3 `}
        style={{ zIndex: 1 }}
      >
        <h1 className="text-center mb-5" style={{ color: "#5260AB" }}>
          {t('login.title')}
        </h1>
        <Formik
          initialValues={{ email: "", password: "" }}
          validationSchema={Validation().LoginSchema}
          onSubmit={(values, { setSubmitting }) => {
            setTimeout(() => {
              alert(JSON.stringify(values, null, 2));
              setSubmitting(false);
            }, 400);
          }}
        >
          {({ isSubmitting, errors }) => (
            <Form>
              <label htmlFor="email" className={`${styles.bgLight} py-1 px-2  `}>
                  {t('login.emailLabel')}
              </label>
              <Field
                type="email"
                name="email"
                className={`d-block mb-3 w-100 p-3 ${
                  errors.email ? `${styles.error}` : ""
                }`}
              />
              <ErrorMessage name="email" component="div"  className={styles.invalid} />
              <label htmlFor="password" className={`${styles.bgLight} py-1 px-2`}>
                  {t('login.passwordLabel')}
              </label>
              <Field
                type="password"
                name="password"
                className={`d-block mb-3 w-100 p-3 ${
                  errors.password ? `${styles.error}` : ""
                }`}
              />
              <ErrorMessage
                name="password"
                component="div"
                className={styles.invalid}
              />
              <label className="d-block mb-3 " htmlFor="remberme">
                <Field type="checkbox" name="remberme" className="ms-2" />
                {t('login.checkBox')}
              </label>
              <button
                type="submit"
                disabled={isSubmitting}
                className={`btn ${styles.btnPrimary} w-100 p-3`}
              >
                 {t('login.login')}
              </button>
            </Form>
          )}
        </Formik>
        <h3 className="text-center py-4" style={{ color: "rgb(175 183 223)" }}>
          أو
        </h3>
        <div className={`${styles.social}  d-flex align-items-center justify-content-between text-center flex-wrap  `}>
          <a
            href="#"
            className="d-flex align-items-center justify-content-center px-5 py-3 fs-5 mb-3"
          >
            <FontAwesomeIcon icon={faTwitter} />
          </a>

          <a
            href="#"
            className="d-flex align-items-center justify-content-center py-3 px-5 fs-5 mx-3 mb-3"
          >
            <FontAwesomeIcon icon={faFacebookSquare} />
          </a>
          <a
            href="#"
            className="d-flex align-items-center justify-content-center py-3 px-5 fs-5 mb-3"
          >
            <img src={google} alt="googleImage" />
          </a>
        </div>
        <div className={styles.signup}>
          <p className="text-center p-3 ">
            <a href="#">  {t('login.createAccount')}</a>
            <a href="#">  {t('login.newAccount')}</a>
          </p>
          <p className="text-center">
            <a href="#"> {t('login.forgetPassword')}</a>
          </p>
        </div>
      </div>
      <div className={styles.down}>
        <img src={group342} alt="circles" className="w-100" />
      </div>
    </div>
  );
}
